import React from 'react'
import { Route, useRouteMatch } from 'react-router-dom'
import Container from 'components/layout/Container'
import Page from 'components/layout/Page'
import { Heading } from '@pancakeswap-libs/uikit'
import { TranslateString } from 'utils/translateTextHelpers'
import IfoTabButtons from './components/IfoTabButtons'
import CurrentIfo from './CurrentIfo'
import PastIfo from './PastIfo'

const Ifos = () => {
  const { path } = useRouteMatch()

  return (
    <Page>
      <Heading as="h1" size="xl" color="primary" mb="10px" style={{ textAlign: 'center' }}>
        {TranslateString(999, 'Initial Token Offerings')}
      </Heading>
      <Heading as="h1" size="lg" color="secondary" mb="30px" style={{ textAlign: 'center' }}>
        {TranslateString(999, 'Buy new tokens with a brand new token sale model.')}
      </Heading>
      <Container>
        <IfoTabButtons />
        <Route exact path={`${path}`}>
          <CurrentIfo />
        </Route>
        <Route path={`${path}/history`}>
          <PastIfo />
        </Route>
      </Container>
    </Page>
  )
}

export default Ifos
